import React from 'react';
import PropTypes from 'prop-types';
//
import { useBool } from '../../../../_hooks/useBool';
//
import IconCaret from '../../../../_icons_svg/_icon_caret/IconCaret';
//
import FooterLinkArr from '../../link_arr/FooterLinkArr';
//
import './FooterPhone.scss';

//
const LINK_ARR_1 = [
    { name: 'Lch s mua hng', link_to: '/phone-laptop' },
    { name: 'Cng tc bn hng cng TGD', link_to: '/phone-laptop' },
    { name: 'Tm hiu v mua tr gp', link_to: '/phone-laptop' },
    { name: 'Chnh sch bo hnh', link_to: '/phone-laptop' },
];

//
const LINK_ARR_1_MORE = [
    { name: 'Chnh sch i tr', link_to: '/phone-laptop' },
    { name: 'Giao hng & Thanh ton', link_to: '/phone-laptop' },
    { name: 'Hng dn mua online', link_to: '/phone-laptop' },
    { name: 'Bn hng doanh nghip', link_to: '/phone-laptop' },
    { name: 'Phiu mua hng', link_to: '/phone-laptop' },
    { name: 'In ha n in t', link_to: '/phone-laptop' },
];

const LINK_ARR_2 = [
    { name: 'Gii thiu cng ty (MWG.vn)', link_to: '/phone-laptop' },
    { name: 'Tuyn dng', link_to: '/phone-laptop' },
    { name: 'Gi gp , khiu ni', link_to: '/phone-laptop' },
    { name: 'Tm siu th (2.736 shop)', link_to: '/phone-laptop' },
    { name: 'Xem bn mobile', link_to: '/phone-laptop' },
];

const LINK_ARR_3 = [
    {
        title: 'Gi mua:',
        phone: '1800.0000',
        time_str: '7:30 - 22:00',
        link_to: '/phone-laptop',
    },
    {
        title: 'K thut:',
        phone: '1800.0001',
        time_str: '7:30 - 22:00',
        link_to: '/phone-laptop',
    },
    {
        title: 'Khiu ni:',
        phone: '1800.0002',
        time_str: '8:00 - 21:30',
        link_to: '/phone-laptop',
    },
    {
        title: 'Bo hnh:',
        phone: '1800.0003',
        time_str: '8:00 - 21:00',
        link_to: '/phone-laptop',
    },
].map((item, ix) => {
    return {
        name: (
            <div className="display-flex">
                <div className="FooterPhone_phone_label">{item.title}</div>

                <div className="margin-right-3px text-blue">{item.phone}</div>

                <div>({item.time_str})</div>
            </div>
        ),
        link_to: item.link_to,
    };
});

//
FooterPhone.propTypes = {};

//
function FooterPhone(props) {
    //
    const { is_true, toggleBool } = useBool();

    //
    return (
        <div className="FooterPhone bg-primary padding-y-15px border-top-blur">
            <div className="FooterPhone_row display-flex flex-wrap fashion-width">
                <div className="FooterPhone_col">
                    <FooterLinkArr
                        link_arr={
                            is_true
                                ? [...LINK_ARR_1, ...LINK_ARR_1_MORE]
                                : LINK_ARR_1
                        }
                    />

                    <div className="margin-bottom-10px cursor-pointer" onClick={toggleBool}>
                        <span className="margin-right-10px">
                            {is_true ? 'Thu gn' : 'Xem thm'}
                        </span>

                        <IconCaret
                            size_icon="14px"
                            class_icon={`${is_true ? 'rotate-180' : ''}`}
                        />
                    </div>
                </div>

                <div className="FooterPhone_col">
                    <FooterLinkArr link_arr={LINK_ARR_2} />
                </div>

                <div className="FooterPhone_col">
                    <div className="margin-bottom-10px">
                        <strong>Tng i h tr</strong>

                        <span className="margin-left-3px">(Min ph gi)</span>
                    </div>

                    <FooterLinkArr link_arr={LINK_ARR_3} />
                </div>

                <div className="FooterPhone_col"></div>
            </div>
        </div>
    );
}

export default FooterPhone;
